import useCountries from '../utils/useCountries';

export default function CountrySelector() {
  const countries = useCountries('https://covid19.mathdro.id/api/countries');
  const countriesList = countries.map((country) =>
    <li>{country}</li>
  );
  return (
    <div>
      <ul>
        {countriesList}
      </ul>
    </div>
  )
}